body {
    background-color: rgb(250, 250,250);
  }
  
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .image {
    width: 370px;
    margin-top: 50px;
  }
  
  @media only screen and (max-width: 770px) {
    .container {
      margin: 0 10%;
    }
  
    .image {
      width: 100%;
    }
  }

.footer {
  display: flex;
  justify-content: center;
  padding: 20px 0;
  font-family: Arial, Helvetica, sans-serif;
}

.footer a {
  color: #000;
}